// axios
import axios from 'axios'
// ant-design-vue notification
import {notification} from 'ant-design-vue'
// 1. axios
const request = axios.create({
  // --
  // baseURL: 'http://akaishuichi.gitee.io/blog/',
  //  ms3s
  timeout: 3 * 1000
})
// 2.
request.interceptors.request.use(config => {
  // token,loading
  config.data = JSON.stringify(config.data) // ,qs
  config.headers = {
    // 'Content-Type': 'application/x-www-form-urlencoded' // 
    'Content-Type': 'application/json'
  }
  // tokencookielocalStoragejs-cookie
  // const token = getCookie('') // tokentoken,
  // if (token) {
  //   config.params = {'token': token} // 
  //   config.headers.token = token // 
  // }
  return config
}, error => {
  Promise.reject(error)
})

// 3.
request.interceptors.response.use(response => {
  // loading
  return response.data
}, error => {
  /**  */
  if (error && error.response) {
    // 1.
    // 2.
    switch (error.response.status) {
      case 400:
        error.message = ''
        break
      case 401:
        error.message = ''
        break
      case 403:
        error.message = ''
        break
      case 404:
        error.message = ','
        window.location.href = '/NotFound'
        break
      case 405:
        error.message = ''
        break
      case 408:
        error.message = ''
        break
      case 500:
        error.message = ''
        break
      case 501:
        error.message = ''
        break
      case 502:
        error.message = ''
        break
      case 503:
        error.message = ''
        break
      case 504:
        error.message = ''
        break
      case 505:
        error.message = 'http'
        break
      default:
        error.message = `${error.response.status}`
    }
  } else {
    // 
    if (JSON.stringify(error).includes('timeout')) {
      notification.error({
        placement: 'bottomRight',
        bottom: '50px',
        duration: 3,
        description: ''
      })
    }
    error.message('')
  }

  notification.error({
    placement: 'bottomRight',
    bottom: '50px',
    duration: 3,
    description: error.message
  })
  /**  */
  // 
  return Promise.resolve(error.response)
})
// 4.
export default request
